@use "functions/escape-svg" as *;
@use "mixins/clearfix" as *;
@use "mixins/color-mode" as *;
@use "mixins/transition" as *;
@use "variables" as *;

/* Notes on the classes:

1. .carousel.pointer-event should ideally be pan-y (to allow for users to scroll vertically)
   even when their scroll action started on a carousel, but for compatibility (with Firefox)
   we're preventing all actions instead

2. The .carousel-item-start and .carousel-item-end is used to indicate where
   the active slide is heading.\

3. .active.carousel-item is the current slide.

4. .active.carousel-item-start and .active.carousel-item-end is the current
   slide in its in-transition state. Only one of these occurs at a time.

5. .carousel-item-next.carousel-item-start and .carousel-item-prev.carousel-item-end
   is the upcoming slide in transition. */

.carousel {
    position: relative;
}

.carousel.pointer-event {
    touch-action: pan-y;
}

.carousel-inner {
    position: relative;
    overflow: hidden;
    inline-size: 100%;

    @include clearfix;
}

.carousel-item {
    position: relative;

    float: left;
    display: none;

    inline-size: 100%;
    margin-inline-end: -100%;

    backface-visibility: hidden;

    @include transition($carousel-transition);
}

.carousel-item.active,
.carousel-item-next,
.carousel-item-prev {
    display: block;
}

.carousel-item-next:not(.carousel-item-start),
.active.carousel-item-end {
    transform: translateX(100%);
}

.carousel-item-prev:not(.carousel-item-end),
.active.carousel-item-start {
    transform: translateX(-100%);
}

/* Alternate transitions */
.carousel-fade {
    .carousel-item {
        transform: none;
        opacity: 0;
        transition-property: opacity;
    }

    .carousel-item.active,
    .carousel-item-next.carousel-item-start,
    .carousel-item-prev.carousel-item-end {
        z-index: 1;
        opacity: 1;
    }

    .active.carousel-item-start,
    .active.carousel-item-end {
        z-index: 0;
        opacity: 0;

        @include transition(opacity 0s $carousel-transition-duration);
    }
}

.carousel-control-prev {
    left: 0;
    background-image: if(
        $enable-gradients,
        linear-gradient(90deg, rgba($black, 0.25), rgba($black, 0.001)),
        null
    );
}

.carousel-control-next {
    right: 0;
    background-image: if(
        $enable-gradients,
        linear-gradient(270deg, rgba($black, 0.25), rgba($black, 0.001)),
        null
    );
}

/* Left/right controls for nav */
.carousel-control-prev,
.carousel-control-next {
    position: absolute;
    z-index: 1;
    top: 0;
    bottom: 0;

    /* Use flex for alignment (1-3) */
    display: flex; // 1. allow flex styles
    align-items: center; // 2. vertically center contents
    justify-content: center; // 3. horizontally center contents

    inline-size: $carousel-control-width;
    padding: 0;
    border: 0;

    color: $carousel-control-color;
    text-align: center;

    opacity: $carousel-control-opacity;
    background: none;
    filter: var(--#{$prefix}carousel-control-icon-filter);

    @include transition($carousel-control-transition);

    /* Hover/focus state */
    &:hover,
    &:focus {
        color: $carousel-control-color;
        text-decoration: none;
        opacity: $carousel-control-hover-opacity;
        outline: 0;
    }
}

/* Icons for within */
.carousel-control-prev-icon,
.carousel-control-next-icon {
    display: inline-block;

    inline-size: $carousel-control-icon-width;
    block-size: $carousel-control-icon-width;

    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 100% 100%;
}

.carousel-control-prev-icon {
    background-image: escape-svg($carousel-control-prev-icon-bg) #{"/*rtl:" +
        escape-svg($carousel-control-next-icon-bg) + "*/"};
}

.carousel-control-next-icon {
    background-image: escape-svg($carousel-control-next-icon-bg) #{"/*rtl:" +
        escape-svg($carousel-control-prev-icon-bg) + "*/"};
}

/* Optional indicator pips/controls

Add a container (such as a list) with the following class and add an item (ideally a focusable control,
like a button) with data-bs-target for each slide your carousel holds. */

.carousel-indicators {
    position: absolute;
    z-index: 2;
    right: 0;
    bottom: 0;
    left: 0;

    display: flex;
    justify-content: center;

    /* Use the .carousel-control's width as margin so we don't overlay those */
    margin-block-end: 1rem;
    margin-inline: $carousel-control-width;
    padding: 0;

    [data-bs-target] {
        cursor: pointer;

        flex: 0 1 auto;

        box-sizing: content-box;
        inline-size: $carousel-indicator-width;
        block-size: $carousel-indicator-height;
        margin-inline: $carousel-indicator-spacer;
        padding: 0;

        /* Use transparent borders to increase the hit area by 10px on top and bottom. */
        border: 0;
        border-block: $carousel-indicator-hit-area-height solid transparent;

        text-indent: -999px;

        opacity: $carousel-indicator-opacity;
        background-color: var(--#{$prefix}carousel-indicator-active-bg);
        background-clip: padding-box;

        @include transition($carousel-indicator-transition);
    }

    .active {
        opacity: $carousel-indicator-active-opacity;
    }
}

/* Optional captions */
.carousel-caption {
    position: absolute;
    right: (100% - $carousel-caption-width) * 0.5;
    bottom: $carousel-caption-spacer;
    left: (100% - $carousel-caption-width) * 0.5;

    padding-block: $carousel-caption-padding-y;

    color: var(--#{$prefix}carousel-caption-color);
    text-align: center;
}

/* Dark mode carousel */
@mixin carousel-dark() {
    --#{$prefix}carousel-indicator-active-bg: #{$carousel-indicator-active-bg-dark};
    --#{$prefix}carousel-caption-color: #{$carousel-caption-color-dark};
    --#{$prefix}carousel-control-icon-filter: #{$carousel-control-icon-filter-dark};
}

.carousel-dark {
    @include carousel-dark;
}

:root,
[data-bs-theme="light"] {
    --#{$prefix}carousel-indicator-active-bg: #{$carousel-indicator-active-bg};
    --#{$prefix}carousel-caption-color: #{$carousel-caption-color};
    --#{$prefix}carousel-control-icon-filter: #{$carousel-control-icon-filter};
}

@if $enable-dark-mode {
    @include color-mode(dark, true) {
        @include carousel-dark;
    }
}
