@use "./mixins/border-radius" as *;
@use "./mixins/box-shadow" as *;
@use "./mixins/deprecate" as *;
@use "./mixins/reset-text" as *;
@use "./variables" as *;
@use "./vendor/rfs" as *;

/* Base class */
.tooltip {
    /* tooltip-css-vars */
    --#{$prefix}tooltip-zindex: #{$zindex-tooltip};
    --#{$prefix}tooltip-max-width: #{$tooltip-max-width};
    --#{$prefix}tooltip-padding-x: #{$tooltip-padding-x};
    --#{$prefix}tooltip-padding-y: #{$tooltip-padding-y};
    --#{$prefix}tooltip-margin: #{$tooltip-margin};
    --#{$prefix}tooltip-color: #{$tooltip-color};
    --#{$prefix}tooltip-bg: #{$tooltip-bg};
    --#{$prefix}tooltip-border-radius: #{$tooltip-border-radius};
    --#{$prefix}tooltip-opacity: #{$tooltip-opacity};
    --#{$prefix}tooltip-arrow-width: #{$tooltip-arrow-width};
    --#{$prefix}tooltip-arrow-height: #{$tooltip-arrow-height};

    @include rfs($tooltip-font-size, --#{$prefix}tooltip-font-size);

    z-index: var(--#{$prefix}tooltip-zindex);

    display: block;

    margin: var(--#{$prefix}tooltip-margin);

    @include deprecate("`$tooltip-margin`", "v5", "v5.x", true);

    /* Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.
   So reset our font and text properties to avoid inheriting weird values. */
    @include reset-text;
    @include font-size(var(--#{$prefix}tooltip-font-size));

    /* Allow breaking very long words so they don't overflow the tooltip's bounds */
    overflow-wrap: break-word;

    opacity: 0;

    &.show {
        opacity: var(--#{$prefix}tooltip-opacity);
    }

    .tooltip-arrow {
        display: block;
        inline-size: var(--#{$prefix}tooltip-arrow-width);
        block-size: var(--#{$prefix}tooltip-arrow-height);

        &::before {
            content: "";
            position: absolute;
            border-color: transparent;
            border-style: solid;
        }
    }
}

.bs-tooltip-top .tooltip-arrow {
    bottom: calc(-1 * var(--#{$prefix}tooltip-arrow-height));

    &::before {
        top: -1px;
        border-width: var(--#{$prefix}tooltip-arrow-height)
            calc(var(--#{$prefix}tooltip-arrow-width) * 0.5) 0;
        border-block-start-color: var(--#{$prefix}tooltip-bg);
    }
}

/* rtl:begin:ignore */
.bs-tooltip-end .tooltip-arrow {
    left: calc(-1 * var(--#{$prefix}tooltip-arrow-height));
    inline-size: var(--#{$prefix}tooltip-arrow-height);
    block-size: var(--#{$prefix}tooltip-arrow-width);

    &::before {
        right: -1px;
        border-width: calc(var(--#{$prefix}tooltip-arrow-width) * 0.5)
            var(--#{$prefix}tooltip-arrow-height)
            calc(var(--#{$prefix}tooltip-arrow-width) * 0.5) 0;
        border-inline-end-color: var(--#{$prefix}tooltip-bg);
    }
}

/* rtl:end:ignore */

.bs-tooltip-bottom .tooltip-arrow {
    top: calc(-1 * var(--#{$prefix}tooltip-arrow-height));

    &::before {
        bottom: -1px;
        border-width: 0 calc(var(--#{$prefix}tooltip-arrow-width) * 0.5)
            var(--#{$prefix}tooltip-arrow-height);
        border-block-end-color: var(--#{$prefix}tooltip-bg);
    }
}

/* rtl:begin:ignore */
.bs-tooltip-start .tooltip-arrow {
    right: calc(-1 * var(--#{$prefix}tooltip-arrow-height));
    inline-size: var(--#{$prefix}tooltip-arrow-height);
    block-size: var(--#{$prefix}tooltip-arrow-width);

    &::before {
        left: -1px;
        border-width: calc(var(--#{$prefix}tooltip-arrow-width) * 0.5) 0
            calc(var(--#{$prefix}tooltip-arrow-width) * 0.5)
            var(--#{$prefix}tooltip-arrow-height);
        border-inline-start-color: var(--#{$prefix}tooltip-bg);
    }
}

/* rtl:end:ignore */
/* stylelint-disable scss/at-extend-no-missing-placeholder */
.bs-tooltip-auto {
    &[data-popper-placement^="top"] {
        @extend .bs-tooltip-top;
    }

    &[data-popper-placement^="right"] {
        @extend .bs-tooltip-end;
    }

    &[data-popper-placement^="bottom"] {
        @extend .bs-tooltip-bottom;
    }

    &[data-popper-placement^="left"] {
        @extend .bs-tooltip-start;
    }
}
/* stylelint-enable scss/at-extend-no-missing-placeholder */

/* Wrapper for the tooltip content */
.tooltip-inner {
    max-inline-size: var(--#{$prefix}tooltip-max-width);
    padding: var(--#{$prefix}tooltip-padding-y)
        var(--#{$prefix}tooltip-padding-x);

    color: var(--#{$prefix}tooltip-color);
    text-align: center;

    background-color: var(--#{$prefix}tooltip-bg);

    @include border-radius(var(--#{$prefix}tooltip-border-radius));
}
